.planttree-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-gap: 1.5rem;
  width: 60%;
  margin: 0 auto;
}
.planttree-details h1 {
  text-align: center;
  margin: 1rem 0;
}
.choice-card {
  margin: 1.15rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 1px 5px 10px rgb(11, 97, 12);
  cursor: pointer;
  transition: 1s;
}
.choice-card:hover {
  transform: translateY(-2%);
}
.choice-card-image {
  width: 20rem;
  background-position: center;
  background-size: cover;
}
.choice-card ul li {
  padding: 1.2rem 0;
}
.planttree-contact {
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.planttree-contact ul li {
  padding: 1rem 0;
}
.planttree-contact ul li p {
  color: green;
  text-decoration: underline;
}
.planttree-contact-button > button a {
  text-decoration: none;
  color: white;
}
