.tree-tracker {
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  /* border: 0.25rem solid green; */
  border-radius: 0.75rem;
  background-color: white;
  margin: 2rem 0;
}
.tree-tracker h1 {
  margin-bottom: 1rem;
  color: green;
}
.tree-box {
  margin: 0 auto;
  border: 0.25rem solid green;
  border-radius: 0.75rem;
  padding: 2rem 4rem;
  background-color: black;
  color: white;
  /* opacity: 0.75; */
}

.tree-box input {
  /* border-bottom: 0.25rem solid black; */
  outline: 0;
  margin: 0.5rem 0;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
}
