.technology {
  text-align: center;
  margin: 2rem auto;
}
.technology > h1 {
  color: green;
  text-transform: uppercase;
  margin-top: 4rem;
}
.technology p {
  font-size: 1.4rem;
}
.tech-flex {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  width: 60%;
  height: 80vh;
  margin: 2.5rem auto;
}
.tech-flex h1 {
  color: green;
  text-transform: uppercase;
}
.tech-flex p {
  font-size: 1.2rem;
}
.left-tech-flex {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.right-tech-flex {
  margin-top: 7rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.tech-margin {
  margin: 7.5rem 0;
}
.middle-tech-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.left-margin {
  margin-left: 7.5rem;
}
.right-margin {
  margin-right: 7.5rem;
}
.circle {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  border: 0.25rem solid green;
  color: green;
  /* margin: 1rem 0; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.travel {
  margin: 6rem 0;
}
.travel p {
  margin: 0.75rem 0;
}
.travel-grid {
  margin: 4rem;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
}
.left-travel-grid {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.left-travel-grid ul {
  margin: 2rem;
}
.left-travel-grid ul li {
  text-decoration: none;
  list-style: circle;
  /* font-weight: bold; */
  text-align: left;
  margin: 0.25rem;
  font-size: 1.25rem;
}
.right-travel-grid {
  display: flex;
  justify-content: center;
  align-items: center;
}
.right-travel-grid-image {
  width: 25rem;
}
.travel h1 {
  color: green;
  text-transform: uppercase;
}
.treechain-tech {
  margin: 2rem 0;
}
.treechain-tech h3 {
  margin: 1.25rem 0;
}
.treechain-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-gap: 2rem;
  width: 80%;
  margin: 0 auto;
}
.right-treechain-grid img {
  width: 35rem;
}
.sustainable {
  margin: 2rem 0;
}
.left-travel-grid {
  text-decoration: none;
  list-style: circle;
  /* font-weight: bold; */
  text-align: left;
  margin: 0.25rem;
  font-size: 1.25rem;
}
.left-treechain-grid h3 {
  font-size: 1.25rem;
}
.left-treechain-grid ul li {
  text-decoration: none;
  list-style: circle;
  /* font-weight: bold; */
  text-align: left;
  margin: 0.25rem;
  font-size: 1.25rem;
}
.treechain-tech h1 {
  font-size: 2.5rem;
  color: green;
}
