.tree-manager-details {
  text-align: center;
}
.tree-manager-details h1 {
  font-size: 2rem;
  padding: 0.25rem;
  color: green;
}
.tree-manager-details h4 {
  font-size: 1.5rem;
}
.tree-manager-details h4 {
  margin: 0.75rem;
}
.treemanager-card-grid {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  border: 0.15rem solid green;
  border-radius: 0.5rem;
  margin: 2rem auto;
  width: 75%;
}
.treemanager-card-grid p {
  margin: 0.5rem 0;
  font-size: 1.25rem;
  font-weight: 550;
}
.treemanager-card-grid ul li {
  margin: 0 1.25rem;
  font-size: 1.15rem;
}
.treemanager-card-grid button {
  border: 0;
  outline: 0;
  cursor: pointer;
}
