.landing-page-image {
  background: url(images/trees.jfif);
  height: 100vh;
  background-size: cover;
  background-position: center;
  width: 100%;
}
.landing-page-image h1 {
  font-size: 5rem;
  color: white;
  margin: 5rem 0;
  line-height: 7rem;
  text-align: center;
  margin-top: 15rem;
}
.landing-page-image h1 > span {
  color: green;
  background-color: white;
  border-radius: 1rem;
  border: 0.15rem solid green;
  padding: 0.75rem;
}
.app-work {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 3rem 0;
}
.app-work > h1 {
  text-align: center;
  margin: 0 auto;
  color: green;
}
.app-work-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  width: 90%;
  grid-gap: 3rem;
  margin: 2rem auto;
}
.work-grid-1 {
  box-shadow: 1px 5px 10px rgb(11, 97, 12);
  padding: 3.5rem 0.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.work-grid-1 h1 {
  font-size: 1.6rem;
  margin: 0.75rem;
}
.img-width {
  width: 12rem;
}

.app-motive {
  margin: 4rem 0;
}
.app-motive h1 {
  text-align: center;
}
.app-motive-cards-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  grid-gap: 3rem;
  width: 80%;
  margin: 2rem auto;
}
.about-icon{
  color: green;
  font-size: 1.5rem;
  margin-left: 7rem;
  margin-bottom: 1rem;

}
.app-motive-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 1.15rem 1.5rem;
  background-color: transparent;
  border-radius: 1rem;
  border: 2px solid green;
  /* border: 0.15rem solid green;
   */
}
.app-motive-card h2{
  color: green;
 margin-left: 1.5rem;
}
.app-motive-card button {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  margin-left: 6rem;
}
.app-motive h1 {
  color: green;
}
.app-motive-card p {
  font-size: 1.2rem;
  margin: 1rem 0;
}
.get-started-content {
  margin: 2rem auto;
  background-color: rgb(5, 41, 15);
  padding: 2rem 0;
  text-align: center;
  color: green;
}
.get-started-content h1 {
  font-size: 3rem;
  color: white;
}
.get-started-content button {
  font-size: 2.2rem;
  outline: 0;
  border: 0;
  color: white;
  cursor: pointer;
  margin: 1.25rem;
}
