* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
.box {
  width: 100%;
  max-width: 1350px;
  padding: 0 3rem;
  margin: 0 auto;
}
:root {
  font-size: 14px;
}
