.nav {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  background-color: black;

  padding: 1rem;
  border-radius: 1rem;
 
  
}
.left-nav {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.left-nav h2 {
  color: green;
}
.nav-items {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}
.nav-items a {
  text-decoration: none;
  color: white;
  /* font-weight: 700; */
}
.right-nav {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.right-nav a {
  padding: 0 1rem;
  text-decoration: none;
  /* font-weight: 700; */
  color: white;
}
button {
  padding: 1rem;
  background-color: green;
  color: white;
  border-radius: 1rem;
  outline: 0;
  border: 0;
}
.right ul li {
  position: relative;
}
.dropdown-menu ul {
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 1rem;
  width: 100%;
  display: none;
}
.dropdown-menu a {
  width: 100%;
  padding: 0.25rem 0;

  /* display: none; */
}
.tree-link:hover .dropdown-menu {
  display: initial;
}
.dropdown-menu ul li {
  list-style: none;
  text-decoration: none;
}
