#about h2{
  color: green;
  text-align: center;
  margin-top: 7rem;
  font-size: 3rem;
  
}
.about_container{
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}
.about_me{
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-primary),transparent);
  display: grid;
  place-items: center;
}
.about_me_image{
  border-radius: 2rem;
  overflow: hidden;
 transform: rotate(10deg);
 transition: var(--transition);
 margin-left: 30rem;
 margin-bottom: 18rem;
 /* width: 50%; */
 

}
.about_me_image:hover{
  transform: rotate(0);
}
.about_cards{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 1.5rem;
  margin-right: 50rem;
  margin-top: 5rem;
 
  


}
.about_card{
  background: green;
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 3.5rem;
  text-align: center;
  justify-content: center;

  
}
.about_card h5{
  color: white;
  font-size:7rem;
  /* padding: 1rem; */
}

/* .about-icon{
  
  color: white;
  font-size: 2rem;
  margin-bottom: 1rem;
 

 


} */
.about_content h5{
  font-size: 1.5rem;
  margin-bottom: 1rem;
  
}
.about_content small{
  font-size: 1.5rem;
  color: white;
}
.about_content p{
margin:  2rem 0 2.6rem;
color: black;
font-size: 1.3rem;
/* font-weight: bold; */


}
button{
  background: green;
  color: white;
  padding: 1.3rem;
  border-radius: .5rem;
  text-decoration: none;
  
}
@media screen  and (max-width: 1024px){
  .about_container{
     grid-template-columns: 1fr;
     gap: 0;
  }
  .about_me{
      width: 50%;
      margin: 2rem auto 4rem;
  }
  .about_content p{
      margin: 1rem 0 1.5rem;
  }
}
@media screen  and (max-width: 600px){
  
  .about_me{
      width: 65%;
      margin: 0 auto 3rem;
  }
  .about_cards{
      grid-template-columns: 1fr;
  }
  .about_content{
      text-align: center;
  }
  .about_content p{
      margin: 1.5rem 0;
  }

}
