.about-details {
  /* height: 80vh; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: green; */
  border-radius: 1rem;
  padding: 2rem;
  width: 75%;
  margin: 0 auto;
}
.about-details h1 {
  font-size: 2.25rem;
  margin: 0 auto;
  text-align: center;
  align-items: center;
  color: green;
  font-weight: 600;
  
}

.why-us {
  margin: 2rem 0;
}
.why-us h1 {
  text-align: center;
}
.about-grid {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  grid-gap: 3rem;
}
.zaheer-img {
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
}
.grid-portion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.supervise {
  margin: 0.5rem 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.supervise h3 {
  color: green;
}
.supervise-image {
  height: 20rem;
  width: 20rem;
  border-radius: 50%;
}
.why-us h3 {
  color: green;
}
