@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
    margin: 0;
    font-family: 'poppins', sans-serif;
    padding: 0;
    box-sizing: border-box;
}
form h2{
    color: white;
    text-align: center;
    font-size: 30px;
}

.wrapper{
    position: relative;
    width: 400px;
    height: 500px;
    /* background: #0ef; */
    box-shadow: 0 0 100px green;
    border-radius: 20px;
    padding: 40px;
    display: flex;
     
    background: green;
    margin-left: 45rem;
    margin-top: 10rem;
    }
    .wrapper:hover{
        animation: animate 1s linear infinite;
    }
    
    
.form-wrapper{
display: flex;
justify-content: center;
align-items: center;
width: 100%;
height: 100%;
transition: 1s ease-in-out;
}

.inputBox{
    position: relative;
    margin: 30px 0;
    border-bottom: 2px solid white;
}
.inputBox label{
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
    color: white;
    pointer-events: none;
    transition: .5s;
}

.inputBox input{
    width: 320px;
    height: 40px;
    font-size: 16px;
    color: white;
    padding: 0 5px;
    background: transparent;
    border: none;
    outline: none;

}
 .inputBox input:valid ~ label,
  .inputBox input:focus ~ label
{
    top: -5px;

}
.remember{
    margin: -5px 0 15px 5px;

}

.remember label{
    color: white;
    font-size: 14px;
    
    
}
.remember input{
    font-weight: 500;
}

.remember label input{
    accent-color: #0ef;
    
}
.btn button{
    position: relative;
    width: 100%;
    height: 40px;
   
    box-shadow: 0 0 10px green;
    font-size: 16px;
    color: green;
    font-weight: 500;
    cursor: pointer;
    border-radius: 30px;
    border: none;
    outline: none;
    background: white;
}
.signinlink{
    color: white;
    text-align: center;
    margin: 14px 0;
}
.signinBtnlink{
    color: white;
    text-decoration: none;
    margin-left: 5px;
    font-weight: 500;
}
.signinBtnlink:hover{
    text-decoration: underline;
}


