.where-content {
  text-align: center;
}
.where-content h1{
  margin-top: 4rem;
}
.where-content p {
  font-size: 1.25rem;
  width: 50%;
  text-align: center;
  margin: 1rem auto;
}
.where-grid-portion {
  margin: 2rem 0;
}
.first-grid-portion {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: auto;
  grid-gap: 1rem;
  width: 80%;
  margin: 1.5rem auto;
  box-shadow: 1px 5px 10px rgb(11, 97, 12);
  padding: 1.25rem 3rem;
}
.first-grid-portion > h1 {
  border-right: 0.25rem solid green;
}

.where-right-grid {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.where-right-grid h1 {
  color: green;
}
.where-right-grid p {
  margin: 1rem 0;
}
